.slider {
  &__wrapper {
    height: calc(100% - 60px);
  }
  &__item {
    height: 100%;
    width: 100%;
    position: relative;
  }
  &__content {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &-wrapper {
      display: flex;
      flex-direction: column;
      justify-content: center;
      p {
        max-width: 580px;
      }
    }
  }
}
.slick {
  &-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
  }
  &-prev {
    left: 20px;
  }
  &-next {
    right: 20px;
  }

  &-list {
    height: 100%;
  }
  &-track {
    height: 100%;
  }

  &-dots {
    width: auto;
    display: flex !important;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 10px;
    left: 75%;
    transform: translateX(-50%);
    li {
      margin-right: 10px;
      list-style-image: none;
      font-size: 0;
      color: transparent;
      &:last-of-type {
        margin-right: 0;
      }
      &:hover {
        button {
          border-color: $cyan;
        }
      }
    }
    li button {
      width: 10px;
      height: 10px;
      border: 1px solid $black;
      border-radius: 50%;
      background-color: $trp;
      font-size: 0;
      cursor: pointer;
      transition: all 0.3s ease-in-out;
    }
  }
  &-active>button { // active dot
      border-color: $cyan !important;
      background-color: $cyan !important;   
  }
}
